.about_wrap {
	height: 100vh;
	margin-top: 10vw;
	margin-bottom: 10vw;
	padding: 0 10vw;

	.inner {
		position: relative;
		display: flex;
		justify-content: space-between;
		padding-top: 65px;
		border-top: 3px solid $primary-color;
	}

	.about_title_wrap {
		flex: 1;
		width: calc(50% - 8px);
		padding-right: 8%;
		margin-right: 8px;

		.head_title__sub {
			font-size: 1.1875em;
			font-weight: 400;
			line-height: 1;
			opacity: 0;
			transform: translateY(100px);
			transition: 0.4s ease-in-out;
			transition-delay: 0.1s;
		}

		.head_title {
			margin-top: 30px;
			font-size: 110px;
			opacity: 0;
			transform: translateX(-100px);
			transition: 0.4s ease-in-out;
			transition-delay: 0.3s;
			transition-duration: 0.3s;
		}

		.btn_wrap {
			margin-top: 90px;
		}
	}
	.about_img_wrap {
		opacity: 0;
		transform: translateX(100px);
		transition: 0.5s ease-in-out;
		transition-delay: 0.7s;
		transition-duration: 0.7s;
		flex: 1;
		width: calc(50% - 8px);
		margin-left: 8px;
		background-color: #d8d8d8;
		border-radius: 20px;
		position: relative;

		#visual {
			overflow: hidden;
		}

		.swiper-wrapper {
			article {
				// position: absolute;
				// top: 0;
				// left: 100%;
				width: 100%;
				height: 85vh;
				z-index: 3;
				transition: 0.7s ease;

				img {
					width: 100%;
					height: 100%;
					object-fit: cover;
				}
			}
		}
		.swiper-option {
			position: absolute;
			bottom: 0;
			right: calc(100% + 10px);
			display: flex;
			align-items: center;
			justify-content: center;
			gap: 10px;

			* {
				position: relative;
				left: auto;
				right: auto;
				top: auto;
				bottom: auto;
				margin: 0;
				flex: 1 1 50px;
				width: 50px;
				height: 50px;
				border-radius: 50px;
				border: 1px solid #1d1d1d;
				text-align: center;
				color: #1d1d1d;
				line-height: 50px;
				font-size: 13px;
				cursor: pointer;
				transition: 0.3s;
			}
			.swiper-button-prev:after,
			.swiper-button-next:after {
				font-size: 20px;
			}
			span.on {
				background: #1d1d1d;
				color: #fff;
			}
		}
	}
