}

.on {
	.about_wrap {
		.about_title_wrap {
			.head_title__sub {
				opacity: 1;
				transform: translateY(0);
			}
			.head_title {
				opacity: 1;
				transform: translateX(0);
			}
		}
		.about_img_wrap {
			opacity: 1;
			transform: translateX(0);
		}
	}
}

@media screen and (max-width: $tablet) {
	.about_wrap {
		height: 100%;
		.inner {
			display: block;
			padding-bottom: 100px;
		}
		.about_title_wrap {
			width: 100%;
			margin: 0;

			.head_title {
				font-size: 12vw;
				line-height: 1;
			}

			.btn_wrap {
				margin-top: 5vw;
			}
		}
		.about_img_wrap {
			width: 100%;
			margin-left: 0;
			margin-top: 60px;

			.swiper-wrapper {
				article {
					height: 50vh;
				}
			}
			.swiper-option {
				bottom: -70px;
				right: auto;
			}
		}
	}
}
