.intro_wrap {
	padding: 157px 10vw;

	.intro_head_wrap {
		display: flex;
		align-items: flex-end;
		justify-content: space-between;

		.head_title__sub {
			padding-bottom: 10px;

			&:last-of-type {
				text-align: right;
			}
		}

		.intro_head {
			flex: 1;
			text-align: center;

			&__sub {
				margin-bottom: 5px;
				font-weight: 400;
				font-size: 15px;
				line-height: 20px;
			}
			.head_wrap {
				// height: 120px;
			}
			.head_title {
				overflow: hidden;
				width: 100%;
				position: relative;
				// animation-name: change;
				// animation-duration: 10s;
				// animation-iteration-count: infinite;height: 4.125rem;
				height: 160px;
				line-height: 160px;
				position: relative;
				width: 100%;
			}
			.head_title > h2 {
				padding-top: 7px;
				overflow: visible;
				margin-top: 0;
				position: absolute;
				top: 0;
				// animation: slide 5s 1s cubic-bezier(0.25, 0.46, 0.45, 0.94) infinite;
			}
			@keyframes slide {
				0% {
					top: 0;
				}
				33% {
					top: -160px;
				}
				66% {
					top: -320px;
				}
				99% {
					top: 0;
				}
			}
		}
	}
	.intro_visual {
		margin-top: 20px;

		&__img {
			// overflow: hidden;
			// width: 100%;
			// height: 695px;
			// border-radius: 16px;
			position: relative;
			overflow: hidden;
			width: 100%;
			// height: 0;
			z-index: 1;
			background-color: gray;
			border-radius: 0.25em;
			// padding-top: 56.25%;

			img,
			video {
				width: 100%;
				height: 100%;
				max-width: none;
				object-fit: cover;
				position: absolute;
				top: 0;
				bottom: 0;
				left: 0;
				right: 0;
			}
		}
	}
	.intro_bottom {
		display: block;
		width: 480px;
		margin: 10vw auto 12vw;
		text-align: center;

		p + p {
			margin-top: 20px;
			font-size: 14px;
			font-weight: normal;
			line-height: 1.3;
			letter-spacing: 0;
		}
	}
}

@media screen and (max-width: $tablet) {
}

@media screen and (max-width: $mobile) {
	.intro_wrap .intro_bottom {
		width: 100%;
		padding: 0 12vw;

		p {
			font-size: 13px;
		}

		p + p {
			font-size: 12px;
		}
	}
}
